:root {
  --small-border-radius: 4px;
  --large-border-radius: 6px;

  --fast-transition: 0.15s ease;
  --slow-transition: 0.4s ease;

  --vertical-padding: 12px;
  --horizontal-padding: 16px;

  --small-box-shadow: 0 7px 14px 0 rgba(50, 50, 93, .1), 0 3px 6px 0 rgba(0, 0, 0, .07);
  --large-box-shadow: 0 10px 20px rgba(127, 130, 140, 0.09), 0 6px 6px rgba(127, 130, 140, 0.12);

  --light-green: #c9debc;
  --medium-green: #b7d2a4;
  --text-green: #266b20;
  --accent-green: #85bf33;

  --light-red: #f4c2c4;
  --medium-red: #efafb1;
  --text-red: #d60028;
  --accent-red: #d60028;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

   background-color: #fff;
   /* background-color: #241f33; */
}

#root {
  display: flex;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  font-weight: bold;
}

.flex-row {
  display: flex;
  flex: 1;
}
.flex-col {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.icon,
.logo {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;

  cursor: pointer;
}
.icon-16 {
  width: 16px;
  height: 16px;
}
.icon-24 {
  width: 24px;
  height: 24px;
}
.icon-32 {
  width: 32px;
  height: 32px;
}

.icon-glass {
  background-image: url('icons/glass-gray.svg');
  background-size: 120%;
}
.icon-star {
  background-image: url('icons/star-yellow.svg');
}
.icon-star-outline {
  background-image: url('icons/star-black.svg');
}

.icon-x {
  background-image: url('icons/x-gray.svg');
}
