.search {
  position: relative;
  margin: var(--vertical-padding) var(--horizontal-padding);
  box-sizing: border-box;

  transition: all var(--fast-transition);

  cursor: pointer;
  user-select: auto;
}

.search .bar {
  height: 42px;
  padding: 0 10px;
  box-sizing: border-box;
  border-radius: var(--small-border-radius);
  align-items: center;
  background: #fff;
  /* box-shadow: 0px 3px 0px 0px rgba(226, 228, 231, 0.75); */
  box-shadow: var(--small-box-shadow);

  transition: all var(--fast-transition);
}

.search .bar input {
  flex: 1;
  height: 24px;
  margin: 0 12px;
  background: none;
  border: none;

  font-size: 14px;
  letter-spacing: inherit;
}
.search .bar input:focus {
  outline: none;
}

.search .bar .icon-x {
  height: 100%;
  padding-left: var(--horizontal-padding);

  opacity: 0;
  transition: var(--fast-transition);
  background-size: 66%;

  pointer-events: none;
  cursor: text;
}

.search .bar.active .icon-x {
  opacity: 1;

  pointer-events: auto;
  cursor: pointer;
}
