.star .icon {
  position: absolute;
  background-size: 60%;

  transition: opacity var(--fast-transition);
  -webkit-tap-highlight-color: transparent;
}
.star .icon-star-outline {
  opacity: 1;
}
.star.active .icon-star-outline {
  opacity: 0;
}
.star .icon-star {
  opacity: 0;
}
.star.active .icon-star {
  opacity: 1;
}
