.card {
  overflow: hidden;
  display: flex;
  margin: var(--vertical-padding) var(--horizontal-padding);
  /* padding: var(--vertical-padding) var(--horizontal-padding); */
  box-sizing: border-box;

  border-radius: var(--small-border-radius);
  /* box-shadow: 0px 3px 0px 0px rgba(226, 228, 231, 0.75); */
  box-shadow: var(--small-box-shadow);
  background-color: #fff;

  font-size: 14px;
  line-height: 1.5;

  transition: transform var(--slow-transition);
}
.card.green {
  background-image: linear-gradient(to top right, #00e17f, #00c161);
}
.card.red {
  background-image: linear-gradient(to top right, #fa7b6f, #f55950);
}

.card .content {
  flex: 1;
}

.card .content .title {
  display: flex;
  margin: 0;
  /* height: 80px; */
  padding: var(--vertical-padding) var(--horizontal-padding);
  margin-bottom: var(--vertical-padding);

  font-size: 28px;
  /* align-items: center; */

  color: #fff;
}
.card.green .content .title {
  /* background-color: var(--medium-green); */
}
.card.red .content .title {
  /* background-color: var(--medium-red); */
}

.card .content .subtitle {
  padding: var(--vertical-padding) var(--horizontal-padding);
  box-sizing: border-box;
}

.card .controls {
  position: relative;
  flex-basis: 48px;
  padding: var(--vertical-padding) var(--horizontal-padding);
  padding-left: 0;
  box-sizing: border-box;
}

.card .controls .star {
  position: absolute;
  top: 0;
  right: 0;
  padding: var(--vertical-padding) var(--horizontal-padding);
}
